@import '~assets/styles/h3Imports';

.modal-overlay {
  background: var(--modal-overlay-background);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;

  --modal-spacing: var(--spacing-xs);
  --buttons-padding-small: 14px 15px;
  --bordered-buttons-padding-small: 13px 16px;
  @media (min-width: 696px) {
    --modal-spacing: var(--spacing-m);
  }

  @media #{$large-up} {
    --modal-spacing: var(--spacing-l);
  }

  --button-min-width: 208px;
}

.heading {
  margin-bottom: var(--spacing-s);
  padding-right: 70px;

  > :first-child {
    margin-bottom: 0;
    margin-top: 0;
  }
  @media #{$tablet-down} {
    margin-top: 32px;
    padding-right: 0;
  }
}

.heading-with-image {
  margin: var(--spacing-xs) 0;
  padding-right: 0;
  text-align: center;

  @media #{$tablet-up} {
    margin-top: var(--spacing-s);
  }

  @media #{$large-up} {
    margin-top: var(--spacing-m);
  }
}

.modal-window {
  background: #ffffff;
  border-radius: 24px;
  box-shadow: var(--box-shadow-modal);
  margin: auto 16px;
  max-height: calc(100% - 40px);
  max-width: rem(472);
  overflow-y: auto;
  padding: 16px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);

  @media (min-width: 696px) {
    margin: 0 auto auto;
  }

  @media #{$large-up} {
    margin: auto;
    max-width: rem(664);
    top: 50%;
    transform: translateY(-50%);
  }

  [data-is-centered] {
    text-align: center;
  }

  [data-is-contained] {
    max-width: rem(584);
  }

  [data-is-centered][data-is-contained] {
    margin-left: auto;
    margin-right: auto;
  }

  [data-modal-hasimage='false'] {
    [data-modal-section='overflow-content'] {
      max-height: 400px;
      overflow: auto;
    }
  }

  [data-modal-section='content'] {
    :first-child {
      margin-top: 0;
    }

    :last-child {
      margin-bottom: 0;
    }
  }

  @media #{$small-down} {
    h2 {
      font-size: rem(24);
    }
  }

  @media #{$tablet-up} {
    h2 {
      font-size: rem(21);
    }
  }
}

.header-image {
  display: flex;
  height: 210px;
  justify-content: center;
  margin-top: var(--spacing-m);

  @media #{$tablet-up} {
    margin-top: var(--spacing-s);
  }
}

.close-button {
  @include interactive-text;

  align-items: center;
  background: transparent;
  border: none;
  color: var(--close-button-color);
  display: flex;
  line-height: 1;
  min-height: 24px;
  min-width: 70px;
  padding: 0;
  position: absolute;
  right: var(--modal-spacing);
  top: var(--modal-spacing);

  &:focus-visible {
    outline: 2px solid var(--focus-button-outline-color, #5995b3);
  }
}

.close-svg {
  margin-right: var(--spacing-xxxs);
}

.buttons-container {
  [data-modal-hasimage='false'] & {
    border-top: 1px solid var(--modal-button-border);
    margin-top: var(--spacing-xs);
    padding-top: var(--spacing-s);

    @media #{$tablet-up} {
      margin-top: var(--spacing-s);
    }
  }

  [data-modal-hasimage='true'] &,
  [data-modal-hasimage-layout='true'] & {
    border-top: none;
    margin-top: var(--spacing-xs);
    padding-top: 0;
    @media #{$tablet-up} {
      margin-top: var(--spacing-s);
    }
    @media #{$large-up} {
      margin-top: var(--spacing-m);
    }
  }

  &[data-button-multiple='false'] {
    @media #{$tablet-only} {
      --button-min-width: 100%;
    }
    @media #{$medium-only} {
      --button-min-width: 100%;
    }
  }
}

.buttons {
  display: flex;
  justify-content: center;

  --button-min-width: 12.25rem;

  @media #{$large-up} {
    --button-min-width: 8.5rem;
  }

  @media #{$small-down} {
    flex-direction: column;

    button,
    a {
      font-size: rem(18);
      height: auto;
      margin-bottom: 0;
      margin-right: 0;
      max-height: rem(48);

      &:first-child {
        margin-bottom: var(--spacing-xs);
      }
    }
  }

  @media #{$tablet-up} {
    button,
    a {
      margin-bottom: 0;
    }
  }
}

.text {
  max-width: rem(584);
  text-align: center;
}

.buttons-desktop {
  display: none;

  &-all {
    display: flex;
  }
  @media #{$tablet-up} {
    display: flex;
  }
}

.buttons-mobile {
  @media #{$tablet-up} {
    display: none;
  }
}
